
.Container{

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 10px;

}

.Container img{ 

    min-height: 350px;
    min-width: 350px;
    max-height: 600px;
    max-width: 600px;
    overflow: hidden;
    text-align: center;
    justify-content: center;
    animation: cssAnimation 0s 2s forwards;
    opacity: 0; 
    display: inline;
    

}

.TextArea{

    vertical-align: middle;
    text-align: center;
    display: inline;      
    width: 400px;
    height: 400px;
    animation: cssAnimation 0s 2s forwards;
    opacity: 0; 

}

.TextArea h3{

    font-size: 40px;

}

.TextArea p{

    font-size: 23px;

}

@keyframes cssAnimation {
    to   { opacity: 1; }
  }