@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;800&display=swap');

.Causes-Container{
  
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    row-gap: 50px;

}

.Causes-Container img{

    padding-left: 30px;
    padding-right: 30px;
    min-height: 50px;
    min-width: 50px;
    max-height: 100px;
    max-width: 100px;
    overflow: hidden;
    text-align: center;
    justify-content: center;
    animation: cssAnimation 0s 2s forwards;
    opacity: 0; 
    display: inline;
}

@keyframes cssAnimation {
    to   { opacity: 1; }
  }

.Causes-Container p{

    font-family: 'Inter', sans-serif;
    font-size: 20px;
    font-weight: 400;
    animation: cssAnimation 0s 2s forwards;
    opacity: 0; 

}