.AreaWaste{
  height: 95vh;
}

.BetterHero{

  background: url(BetterHeroBG.svg) no-repeat center center; 
  height: 100vh;
  background-size:     cover;                      
  background-repeat:   no-repeat;
  background-position: center center;
  /* background-attachment: fixed; */
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  
}

.text{

  margin-top: 120px;
  font-size: 42px;
  font-weight: 600;
  color: rgba(251,121,57,254);
  font-family: 'Inter', sans-serif;
  animation: cssAnimation 0s 2s forwards;
  opacity: 0; 
}

@keyframes cssAnimation {
  to   { opacity: 1; }

}

.Img {
  height: 140px;
  margin-top: -120px;
  -webkit-animation:mymove 2s 1; /* Safari and Chrome */
  animation:mymove 2s 1;
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  z-index: 1;
}
  
  
  @keyframes mymove
  {
  0%   {top:0px;}
  35%  {top:300px;}
 
  100% {top:120px;}
  }
  
  @-webkit-keyframes mymove /* Safari and Chrome */
  {
  0%   {top:0px;}
  35%  {top:300px;}
  
  100% {top:120px;}
  }


.wrapper {

  height: 60px;
  display: flex;
  justify-content: center;
  animation: cssAnimation 0s 2s forwards;
  opacity: 0; 
  
}
@keyframes cssAnimation {
  to   { opacity: 1; }

}

.cta {
    display: flex;
    padding: 8px 20px;
    text-decoration: none;
    font-family: 'Poppins', sans-serif;
    font-size: 30px;
    color: white;
    background: rgb(253, 149, 3);
    transition: 1s;
    box-shadow: 6px 6px 0 black;
    transform: skewX(-15deg);
}

.cta:focus {
   outline: none; 
}

.cta:hover {
    transition: 0.5s;
    box-shadow: 10px 10px 0 black;
}

.cta span:nth-child(2) {
    transition: 0.5s;
    margin-right: 0px;
}

.cta:hover  span:nth-child(2) {
    transition: 0.5s;
    margin-right: 45px;
}

  span {
    transform: skewX(15deg) 
  }

  span:nth-child(2) {
    width: 20px;
    margin-left: 30px;
    position: relative;
    top: 12%;
  }
  
/**************SVG****************/

path.one {
    transition: 0.4s;
    transform: translateX(-60%);
}

path.two {
    transition: 0.5s;
    transform: translateX(-30%);
}

.cta:hover path.three {
    animation: color_anim 1s infinite 0.2s;
}

.cta:hover path.one {
    transform: translateX(0%);
    animation: color_anim 1s infinite 0.6s;
}

.cta:hover path.two {
    transform: translateX(0%);
    animation: color_anim 1s infinite 0.4s;
}

/* SVG animations */

@keyframes color_anim {
    0% {
        fill: white;
    }
    50% {
        fill: #FBC638;
    }
    100% {
        fill: white;
    }
}